.mes{
   color: #444;
   font-size: large;
   text-align: center;
   font-weight: bold;
   margin: 0.5em 0.5em 0.5em 0.5em;
   padding: 0.5em 0.5em 0.5em 0.5em;
   background-color: #ffffff;
   border-radius: 5px 5px 5px 5px;
   border-bottom: 0.1px solid #444;
}
.sidebar {
    position: fixed;
    top: 10.2vh;
    right: 0vw;
    width: 15vw;
    min-height: 100vh;
    background-color: rgb(240, 208, 212);
    box-shadow: 0 2px 3px #444;
    z-index: -1;
}

.sidebar h2 {
    text-align: center;
}

.sidebar ul {
    padding-inline-start: 0em;
}

.month-list p{
    padding-inline-start: 0.5em;
}

.sidebar li {
    margin: 0.7em 0.5em 0.5em 0.7em;
    padding: 0.5em 0.5em 0.5em 0.5em;
    text-align: center;
    list-style: none;
    background-color: rgb(238, 221, 223);
    border-bottom: 0.1px solid #444;
    border-radius: 5px 5px 5px 5px;}